    }

    @media only screen and (min-width: 734px) {
      .menu-wrap{
        display: none;
      }
     }


    .menu-wrap .toggler{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      cursor: pointer;
      width: 35px;
      height: 35px;
      opacity: 0;
      margin-top: 10px;
      margin-left: 35px;
    }

    .menu-wrap .hamburger{
      position: absolute;
      top: 10px;
      left: 40px;
      z-index: 1;
      width: 30px;
      height: 30px;
      padding: 5px;
      background: rgb(227, 225, 225);
      display: flex;
      align-items: center;
      justify-content: center; 
      border-radius: 5px;
    }

    /* Hamburger line */
    .menu-wrap .hamburger > div {
        position: relative;
        width: 100%;
        height: 2px;
        background-color: rgb(94, 103, 205);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.4s ease;
    }

    /* Top and button lines */
    .menu-wrap .hamburger > div:before,
    .menu-wrap .hamburger > div:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: -7px;
      width: 100%;
      height: 2px;
      background: inherit;
    }

    /* Moves line down */
    .menu-wrap .hamburger > div:after {
      top: 7px;
    }

    /* Toggler animation */
    .menu-wrap .toggler:checked + .hamburger > div {
      transform: rotate(360deg);
    }

  /* Turns line into X */
  .menu-wrap .toggler:checked + .hamburger > div:before,
  .menu-wrap .toggler:checked + .hamburger > div:after {
      top: 0;
      transform: rotate(90deg);
  }

  /* Rotate on hover when checked */
  .menu-wrap .toggler:checked:hover + .hamburger > div {
    transform: rotate(225deg);
  }

  .menu-wrap .menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 400px;
    height: 600px;
    visibility: hidden;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .menu-wrap .menu > div {
     background: rgb(11, 2, 89);
     border-radius: 50%;
     width: 800px;
     height: 800px;
     margin-left: -70px;
     margin-top: -50px;
     display: flex;
     flex: none;
     align-items: center;
     justify-content: center;
     transform: scale(0);
     transition: all 1.0s ease;
  }

  .menu-wrap .menu > div > div {
    text-align: center;
    max-width: 90vw;
    max-height: 100vh;
    opacity: 0;
    transition: opacity 0.4s ease;
  }

  .menu-wrap .menu > div > div > ul > li  {
    list-style: none;
    color: rgb(190, 109, 109);
    font-size: 1.5rem;
    padding: 1rem;
    margin-left: 90px;
  }

  .menu-wrap .menu > div > div > ul > li > a {
    color: inherit;
    text-decoration: none;
    transition: color 0.5s ease;
  }

  /* show menu */
  .menu-wrap .toggler:checked ~ .menu {
    visibility: visible;
  }

  .menu-wrap .toggler:checked ~ .menu > div {
    transform: scale(1);
    transition-duration: 30ms;
  }

  .menu-wrap .toggler:checked ~ .menu > div > div{
    opacity: 1;
    transition: opacity 2.4s ease;
  }